import gql from 'graphql-tag'
import GRAPHQL from './index'

export default class {
  static getUsers(data, callback) {
    const query = {
      query: gql`
        {
          saleGetShopUsers(page: ${data.page}, pageSize: ${data.pageSize}) {
            status
            message
            success
            data
          }
        }
      `,
    }

    GRAPHQL.doQuery(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetShopUsers.data),
          })
        } catch (e) {
          return callback({ status: 0, message: 'HTTPERROR' })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static getUser(id, callback) {
    const query = {
      query: gql`
      {
        saleGetShopUser(user_id:"${id}") {
          status
          message
          success
          data
        }
      }`
    }

    GRAPHQL.doQuery(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetShopUser.data),
          })
        } catch (e) {
          return callback({ status: 0, message: 'HTTPERROR' })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static getUserOptions(keyword, callback) {
    const query = {
      query: gql`
        {
          saleGetShopUsers(page: 1, pageSize: 10, keyword:"${keyword}") {
            status
            message
            success
            data
          }
        }
      `,
    }

    GRAPHQL.doQuery(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetShopUsers.data),
          })
        } catch (e) {
          return callback({ status: 0, message: 'HTTPERROR' })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static postUser(data, callback) {
    console.log('data onpc---->', data)
    const query = {
      mutation: gql`
      mutation {
        salePostShopUser(
          user_id:"${data._id ? data._id : ''}", 
          full_name: "${data.full_name}", 
          email: "${data.email}",    
          user_password: "${data.user_password}",
          wallet_address: "${data.wallet_address}",
          country_code:  "${data.country_code}",
          is_admin:  ${data.is_admin ? 'true' : 'false'},
          is_sorter:  ${data.is_sorter ? 'true' : 'false'},
          is_collector:  ${data.is_collector ? 'true' : 'false'},
          is_deliver:  ${data.is_deliver ? 'true' : 'false'},
        ) {
          status
          message
          success
          data
        }
      }
    `,
    }
    GRAPHQL.doMutate(query)
      .then((response) => {
        console.log('response....', response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostShopUser.data),
          })
        } catch (e) {
          console.log('e on mutate...', e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static deleteUser(user_id, callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteShopUser(
              user_id:"${user_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `,
    }

    GRAPHQL.doMutate(query)
      .then((response) => {
        console.log('response....', response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteShopUser.data),
          })
        } catch (e) {
          console.log('e on mutate...', e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }
}
