import React, { Component, Fragment } from 'react'
import IntlMessages from '../../../helpers/IntlMessages'
import { Colxx, Separator } from '../../../components/common/CustomBootstrap'
import Breadcrumb from '../../../containers/navs/Breadcrumb'
import 'react-tabs/style/react-tabs.css'
import {
  Row,
  Card,
  CardBody,
  Input,
  Button,
  CustomInput,
  Label,
  Form,
} from 'reactstrap'
import 'react-tagsinput/react-tagsinput.css'
import 'react-datepicker/dist/react-datepicker.css'
import 'rc-switch/assets/index.css'
import 'rc-slider/assets/index.css'
import 'react-rater/lib/react-rater.css'
import AsyncSelect from 'react-select/async'
import CustomSelectInput from '../../../components/common/CustomSelectInput'
import moment from 'moment'
import HubService from '../../../service/graphql/Hub'
import StateService from '../../../service/graphql/State'
import ShopService from '../../../service/graphql/Shop'
import CityService from '../../../service/graphql/City'
import CountryService from '../../../service/graphql/Country'
import UserService from '../../../service/graphql/User'
import { NotificationManager } from '../../../components/common/react-notifications'
import {
  plainListToGql,
  gqlToLanguageOptions,
  getDefaultLanguageOptions,
} from '../../../libs/converter'
import LanguageSelector from '../../../components/LanguageSelector'
import Config from '../../../service/api/config'
export default class BlankPage extends Component {
  constructor(props) {
    super(props)

    this.state = {
      selectedOption: '',
      selectedOptionLabelOver: '',
      selectedOptionLabelTop: '',
      startDate: null,
      startDateLabelOver: null,
      startDateLabelTop: null,
      startDateTime: null,
      startDateRange: null,
      endDateRange: null,
      embeddedDate: moment(),
      tags: [],
      tagsLabelOver: [],
      tagsLabelTop: [],
      selectedShopId: null,
      operatorOptions: [],
      selectedItem: {},
      langs: [],
      editState: false,
      langOptions: [],
      selectedLangIndex: 0,
      userOptions: [],
      hubOptions: [],
      countryOptions: [],
      stateOptions: [],
      cityOptions: [],
      isSubmitting: false,
    }
  }
  componentDidMount() {
    console.log('this.props.match on shpo detail', this.props.match.params.id)
    this.setState({ editState: false })
    this.setState({ selectedShopId: this.props.match.params.id })
    this.getStates()
    this.getCountries()
    setTimeout(() => {
      if (this.props.match.params.id && this.props.match.params.id !== '0') {
        ShopService.getShop(this.props.match.params.id, (res) => {
          console.log('product from gotton...', res.data)
          this.getHubs(res.data.hub_id)
          this.getUsers(res.data.shop_user_id)
          this.getCities(res.data.shop_city_id)
          this.setState({ selectedItem: res.data })
          this.setState({
            langOptions: gqlToLanguageOptions(res.data.langs, 'shop_label'),
          })
        })
      } else {
        getDefaultLanguageOptions((options) => {
          this.setState({ langOptions: options })
        })
      }
    }, 1000)
  }
  handleChangeLabelOver = (selectedOptionLabelOver) => {
    this.setState({ selectedOptionLabelOver })
  }
  handleSubmit(e) {
    e.stopPropagation()
    e.preventDefault()
    const { selectedItem } = this.state
    console.log('selectedItem on handle submit...', selectedItem)
    this.setState({ isSubmitting: true })
    if (
      !selectedItem.shop_user_id ||
      !selectedItem.hub_id ||
      !selectedItem.country_code ||
      !selectedItem.shop_state_id ||
      !selectedItem.shop_city_id
    ) {
      return
    }
    this.addNewInstance()
  }
  getUsers = (id) => {
    UserService.getUser(id, (response) => {
      if (response.status === 1) {
        const operator = response.data
        const tempOptions = []
        const obj = {
          label: operator.full_name,
          value: operator._id,
          id: operator._id,
        }
        tempOptions.push(obj)
        this.setState({ userOptions: tempOptions })
      }
    })
  }
  getSelectedOption(value, type) {
    console.log('value, type---->', value, type)
    let options = []
    if (type === 'user') {
      options = this.state.userOptions
    }
    if (type === 'city') {
      options = this.state.cityOptions
    }
    if (type === 'state') {
      options = this.state.stateOptions
    }
    if (type === 'hub') {
      options = this.state.hubOptions
    }
    if (type === 'country') {
      options = this.state.countryOptions
    }

    const found = options.find((item) => {
      return item.value === value
    })
    console.log('found----options', found, this.state.userOptions)
    return found
  }
  getHubs(id) {
    HubService.getHub(id, (response) => {
      if (response.status === 1) {
        const operator = response.data
        const tempOptions = []

        const obj = {
          label: operator.hub_identifier,
          value: operator._id,
          id: operator._id,
        }
        tempOptions.push(obj)

        this.setState({ hubOptions: tempOptions })
      }
    })
  }
  getCountries = () => {
    CountryService.getCountries(
      { page: 1, pageSize: 1000, keyword: '' },
      (response) => {
        if (response.status === 1) {
          const operators = response.data.results
          const tempOptions = []
          operators.forEach((element) => {
            const obj = {
              label: element.country_code,
              value: element.country_code,
              id: element._id,
            }
            tempOptions.push(obj)
          })
          console.log('countryOptions.....', tempOptions)
          this.setState({ countryOptions: tempOptions })
        }
      }
    )
  }
  getStates = () => {
    StateService.getStates(1, 10, '', (response) => {
      if (response.status === 1) {
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.state_name,
            value: element._id,
            id: element._id,
          }
          tempOptions.push(obj)
        })
        this.setState({ stateOptions: tempOptions })
      }
    })
  }

  getCities = (id) => {
    CityService.getCity(id, (response) => {
      if (response.status === 1) {
        const city = response.data
        const tempOptions = []
       
          const obj = {
            label: city.city_name,
            value: city._id,
            id: city._id,
          }
          tempOptions.push(obj)
       
        this.setState({ cityOptions: tempOptions })
      }
    })
  }

  loadUserOptions(inputValue, callback) {
    UserService.getUserOptions(inputValue, (response) => {
      if (response.status === 1) {
        const users = response.data.results
        console.log('users on load useroptions----->>>> ', users)
        const tempOptions = []
        users.forEach((element) => {
          const obj = {
            label: element.full_name,
            value: element._id,
            id: element._id,
          }
          tempOptions.push(obj)
        })
        callback(tempOptions)
      }
    })
  }
  loadHubOptions(inputValue, callback) {
    HubService.getHubs({ page: 1, pageSize: 10 }, (response) => {
      if (response.status === 1) {
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.hub_identifier,
            value: element._id,
            id: element._id,
          }
          tempOptions.push(obj)
        })
        callback(tempOptions)
      }
    })
  }
  loadStateOptions(inputValue, callback) {
    StateService.getStates(1, 10, inputValue, (response) => {
      if (response.status === 1) {
        console.log('responst on state options....', response)
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.state_name,
            value: element._id,
            id: element._id,
          }
          tempOptions.push(obj)
        })
        callback(tempOptions)
      }
    })
  }
  loadCountryOptions = (inputValue, callback) => {
    CountryService.getCountries({ page: 1, pageSize: 1000, keyword: inputValue }, (response) => {
      if (response.status === 1) {
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.country_code,
            value: element.country_code,
            id: element._id,
          }
          tempOptions.push(obj)
        })

        callback(tempOptions)
      }
    })
  }
  loadCityOptions(inputValue, callback) {
    CityService.getCities(1, 10, inputValue, (response) => {
      if (response.status === 1) {
        console.log('responst on huboption....', response)
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.city_name,
            value: element._id,
            id: element._id,
          }
          tempOptions.push(obj)
        })
        callback(tempOptions)
      }
    })
  }
  loadCountryOptions(inputValue, callback) {
    CountryService.getCountries({ page: 1, pageSize: 1000 }, (response) => {
      if (response.status === 1) {
        console.log('responst on huboption....', response)
        const operators = response.data.results
        const tempOptions = []
        operators.forEach((element) => {
          const obj = {
            label: element.country_name,
            value: element.country_code,
            id: element.country_code,
          }
          tempOptions.push(obj)
        })
        callback(tempOptions)
      }
    })
  }

  initializeSelectedItem() {
    const { selectedItem } = this.state
    selectedItem.shop_identifier = ''
    selectedItem.shop_user_id = ''
    selectedItem.hub_id = ''

    selectedItem.country_code = ''
    selectedItem.shop_phone_number = ''
    selectedItem.shop_state_id = ''
    selectedItem.shop_city_id = ''
    selectedItem.shop_address = ''
    selectedItem.shop_lat = ''
    selectedItem.shop_lon = ''

    this.setState({ selectedItem })
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.match.params.id === '0') {
      console.log('renewed....')
      this.initializeSelectedItem()
    }
  }
  onChangeProperty(value, type) {
    console.log('value,type', value, type)
    const { selectedItem } = this.state
    if (type === 'shop_logo') {
      this.setState({ editState: true })
      let reader = new FileReader()
      reader.readAsDataURL(value)
      reader.onloadend = () => {
        selectedItem[type] = reader.result
        console.log('value on shop_logo--->>>', reader.result)
        this.setState({ selectedItem })
      }
    } else {
      selectedItem[type] = value
      this.setState({ selectedItem })
    }
  }

  onChangeName(value) {
    console.log('e,value on changename---->', value)
    const { selectedLangIndex } = this.state
    const selectedLangOption = this.state.langOptions[selectedLangIndex]
    selectedLangOption.title = value
    this.setState({ langOptions: [...this.state.langOptions] })
  }
  addNewInstance() {
    // this.props.history.push("/app/shops/shops");
    const { selectedItem, editState } = this.state
    if (
      selectedItem.shop_identifier === undefined ||
      selectedItem.shop_identifier.length === 0 ||
      selectedItem.shop_city_id === undefined ||
      selectedItem.shop_city_id.length === 0 ||
      selectedItem.hub_id === undefined ||
      selectedItem.hub_id.length === 0
    ) {
      NotificationManager.error(
        'Error message',
        'Please confirm valid data!',
        3000,
        () => {
          // alert("callback");
        },
        null,
        'error'
      )
      return
    }
    console.log('edit_state..... ', editState)
    if (!editState) {
      selectedItem.shop_logo = ''
    }
    let templangData = plainListToGql(this.state.langOptions)
    this.state.selectedItem.lang_data = templangData
    ShopService.postShop(this.state.selectedItem, (error, res) => {
      this.props.history.push('/app/shops/shop-list')
    })
  }
  render() {
    const { selectedShopId, selectedItem, editState, isSubmitting } = this.state
    return (
      <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb
              heading={selectedShopId === '0' ? 'New Shop' : 'Shop Detail'}
              match={this.props.match}
            />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Form onSubmit={this.handleSubmit.bind(this)}>
          <Row className="mb-4">
            <Colxx xxs="12" md="12">
              <Card>
                <CardBody className="d-flex">
                  <div className="flex-1">
                    <Label className="mt-4">Identifier</Label>
                    <Input
                      required
                      onChange={(e) => {
                        this.onChangeProperty(e.target.value, 'shop_identifier')
                      }}
                      value={selectedItem.shop_identifier}
                    />
                    <Label className="mt-4">User</Label>
                    <AsyncSelect
                      required
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={this.loadUserOptions}
                      defaultOptions
                      value={this.getSelectedOption(
                        selectedItem.shop_user_id,
                        'user'
                      )}
                      onChange={(e) => {
                        this.onChangeProperty(e.value, 'shop_user_id')
                      }}
                      onInputChange={this.handleInputChange}
                    />
                    {isSubmitting && !selectedItem.shop_user_id && (
                      <div className="invalid-feedback d-block">
                        {'Shop user required!'}
                      </div>
                    )}
                    <Label className="mt-4">Hub</Label>
                    <AsyncSelect
                      required
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={this.loadHubOptions}
                      defaultOptions
                      value={this.getSelectedOption(selectedItem.hub_id, 'hub')}
                      onChange={(e) => {
                        this.onChangeProperty(e.value, 'hub_id')
                      }}
                      onInputChange={this.handleInputChange}
                    />
                    {isSubmitting && !selectedItem.hub_id && (
                      <div className="invalid-feedback d-block">
                        {'Hub required!'}
                      </div>
                    )}
                    <Label className="mt-4">Country Code</Label>
                    <AsyncSelect
                      required
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={this.loadCountryOptions}
                      defaultOptions
                      value={this.getSelectedOption(
                        selectedItem.country_code,
                        'country'
                      )}
                      onChange={(e) => {
                        this.onChangeProperty(e.value, 'country_code')
                      }}
                      onInputChange={this.handleInputChange}
                    />
                    {isSubmitting && !selectedItem.hub_id && (
                      <div className="invalid-feedback d-block">
                        {'Country required!'}
                      </div>
                    )}
                    {/* <Input required  onChange={(e)=>{this.onChangeProperty(e.target.value,'country_code')}} value={selectedItem.country_code}/> */}
                    <Label className="mt-4">Phone Number</Label>
                    <Input
                      required
                      onChange={(e) => {
                        this.onChangeProperty(
                          e.target.value,
                          'shop_phone_number'
                        )
                      }}
                      value={selectedItem.shop_phone_number}
                    />
                    <Label className="mt-4">State</Label>
                    <AsyncSelect
                      required
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={this.loadStateOptions}
                      defaultOptions
                      value={this.getSelectedOption(
                        selectedItem.shop_state_id,
                        'state'
                      )}
                      onChange={(e) => {
                        this.onChangeProperty(e.value, 'shop_state_id')
                      }}
                      onInputChange={this.handleInputChange}
                    />
                    {isSubmitting && !selectedItem.shop_state_id && (
                      <div className="invalid-feedback d-block">
                        {'State required!'}
                      </div>
                    )}
                    <Label className="mt-4">City</Label>
                    {/* <Input  onChange={(e)=>{this.onChangeProperty(e.target.value,'shop_city_id')}} value={selectedItem.shop_city_id}/> */}
                    <AsyncSelect
                      required
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={this.loadCityOptions}
                      defaultOptions
                      value={this.getSelectedOption(
                        selectedItem.shop_city_id,
                        'city'
                      )}
                      onChange={(e) => {
                        this.onChangeProperty(e.value, 'shop_city_id')
                      }}
                      onInputChange={this.handleInputChange}
                    />
                    {isSubmitting && !selectedItem.shop_state_id && (
                      <div className="invalid-feedback d-block">
                        {'City required!'}
                      </div>
                    )}
                    <Label className="mt-4">Address</Label>
                    <Input
                      required
                      onChange={(e) => {
                        this.onChangeProperty(e.target.value, 'shop_address')
                      }}
                      value={selectedItem.shop_address}
                    />
                    <div className="d-flex">
                      <Button
                        color="primary"
                        className="mt-4 mr-4"
                        type="submit"
                      >
                        <IntlMessages id="forms.submit" />
                      </Button>
                      <Button
                        color="primary"
                        className="mt-4"
                        onClick={() => {
                          this.props.history.push('/app/shops/shop-list')
                        }}
                      >
                        Close
                      </Button>
                    </div>
                  </div>
                  <div className="flex-1">
                    <Label className="mt-4">Image</Label>
                    <CustomInput
                      type="file"
                      id="exampleCustomFileBrowser1"
                      name="customFile"
                      onChange={(e) => {
                        this.onChangeProperty(e.target.files[0], 'shop_logo')
                      }}
                    />
                    {selectedItem._id &&
                      selectedItem.shop_logo &&
                      !editState && (
                        <img
                          className="shop-image"
                          src={
                            Config.BACKEND_API_URL + selectedItem.shop_logo
                          }
                        />
                      )}
                    {editState && (
                      <img
                        className="shop-image"
                        src={selectedItem.shop_logo}
                      />
                    )}
                    <div>
                      <Label className="mt-4">Latitude</Label>
                      <Input
                        required
                        onChange={(e) => {
                          this.onChangeProperty(e.target.value, 'shop_lat')
                        }}
                        value={selectedItem.shop_lat}
                      />
                    </div>
                    <Label className="mt-4">Longitude</Label>
                    <Input
                      required
                      onChange={(e) => {
                        this.onChangeProperty(e.target.value, 'shop_lon')
                      }}
                      value={selectedItem.shop_lon}
                    />
                    {/* {this.renderLanguageSetting()} */}
                    <LanguageSelector
                      langOptions={this.state.langOptions}
                      onChangeName={this.onChangeName.bind(this)}
                      selectedLangIndex={this.state.selectedLangIndex}
                      onChangeTab={(index) => {
                        this.setState({ selectedLangIndex: index })
                      }}
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>
        </Form>
      </Fragment>
    )
  }
}
