import gql from 'graphql-tag'
import GRAPHQL from './index'

export default class {
  static getCities(page, pageSize, keyword, callback) {
    const query = {
      query: gql`
        {
          saleGetCities(page:${page},pageSize:${pageSize},keyword:"${keyword}"){
          status
          message
          success
          data
        }
       }`,
    }

    GRAPHQL.doQuery(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetCities.data),
          })
        } catch (e) {
          return callback({ status: 0, message: 'HTTPERROR' })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static getCity(id, callback) {
    const query = {
      query: gql`
        {
          saleGetCity(city_id:"${id}"){
          status
          message
          success
          data
        }
       }`,
    }

    GRAPHQL.doQuery(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetCity.data),
          })
        } catch (e) {
          return callback({ status: 0, message: 'HTTPERROR' })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static postCity(data, callback) {
    const query = {
      mutation: gql`
          mutation{
            salePostCity(        
              city_id:"${data._id ? data._id : ''}", 
              city_name: "${data.city_name}",
              state_id: "${data.state_id}",
              lang_data: ${data.lang_data},
            ){
              status
              message
              success
              data
            }
          }
        `,
    }
    GRAPHQL.doMutate(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostCity.data),
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }

  static deleteCity(city_id, callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteCity(
              city_id:"${city_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `,
    }

    GRAPHQL.doMutate(query)
      .then((response) => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteCity.data),
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch((error) => {
        return callback({ status: 0, message: error })
      })
  }
}
