import gql from 'graphql-tag';
import GRAPHQL from './index'


export default class {
  static getStates(page=1, pageSize=100,keyword="", callback) {
    console.log('calling getStates', page, pageSize,keyword);
    const query = {
      query: gql`
        {
          saleGetStates(page:${page},pageSize:${pageSize}, keyword:"${keyword}"){
          status
          message
          success
          data
        }
       }`
    }    
    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetStates.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static postState(data,callback) {
    
    const query = {
      mutation: gql`
          mutation{
            salePostState( 
              state_id:"${data._id?data._id:''}", 
              state_name: "${data.state_name}",
              country_code: "${data.country_code}",
              lang_data: ${data.lang_data},
            ){
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostState.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteState(state_id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteState(
              state_id:"${state_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteState.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
