import gql from 'graphql-tag';
import GRAPHQL from './index'


export default class {
  
  
  static getCountries(data, callback) {
    const query = {
      query: gql`
        {
          saleGetCountries(page:${data.page}, pageSize:${data.pageSize}, keyword:"${data.keyword}"){
          status
          message
          success
          data
        }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetCountries.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }
  static getCountryOptions(callback) {
    const query = {
      query: gql`
        {
          saleGetCountries(page:1,pageSize:15){
          status
          message
          success
          data
        }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetCountries.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static postCountry(data,callback) {
    const query = {
      mutation: gql`
          mutation{
            salePostCountry(       
              country_id:"${data._id?data._id:''}", 
              country_code: "${data.country_code}",
              country_name: "${data.country_name}",
              phone_code: "${data.phone_code}",
              lang_data: ${data.lang_data},
            ) {
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostCountry.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteCountry(country_code,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteCountry(
              country_code:"${country_code}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.getCountries.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
