
const TEST_MODE=1
const CURRENT_MODE = TEST_MODE

class Config {
    constructor() {
        this.config = {}
        this.configReady = false;
        if(CURRENT_MODE===TEST_MODE){
            this.COVID_API_URL = "https://api.testenv.sharetoken.io/covid"
            this.AUTH_API_URL = "https://api.testenv.sharetoken.io/auth"
            this.BACKEND_API_URL = "https://api.testenv.sharetoken.io"
        }
    }
}

export default (new Config);
