import gql from 'graphql-tag';
import GRAPHQL from './index'
import moment from 'moment'

export default class {
  
  
  static getLanguages(callback) {
    const query = {
      query: gql`
        {
          saleGetShopLangs {
          status
          message
          success
          data
        }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetShopLangs.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static postLanguage(data,callback) {
    const query = {
      mutation: gql`
          mutation{
            salePostShopLang(
              lang_code:"${data.lang_code?data.lang_code:''}",
              lang_name: "${data.lang_name}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostShopLang.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteLanguage(lang_code,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteShopLang(
              lang_code:"${lang_code}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteShopLang.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
